import { Box, Container, Link} from "@mui/material"


export const AppBar = () => {
    return (
        <Container maxWidth='lg'>
        <Box
            gap={8.5}
            sx={{marginTop: '25px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}
        >
                <Link color="inherit" href="#">Technology</Link>
                <Link color="inherit" href="#">Design</Link>
                <Link color="inherit" href="#">Culture</Link>
                <Link color="inherit" href="#">Business</Link>
                <Link color="inherit" href="#">Politics</Link>
                <Link color="inherit" href="#">Opinion</Link>
                <Link color="inherit" href="#">Science</Link>
                <Link color="inherit" href="#">Health</Link>
                <Link color="inherit" href="#">Style</Link>
                <Link color="inherit" href="#">Travel</Link>
        </Box>
        </Container>
    )
}