import {
  Card,
  CardContent,
  CardMedia,
  Container,
  Grid,
  Link,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import PostImage from "./../../Assets/images/images.jpg";

export const PostCard = () => {
  return (
    <Container variant="lg">
      <Grid container spacing={5}>
        <Grid item xs={6}>
          <Card sx={{ display: "flex" , justifyContent: "space-between"}}>
            <Box sx={{ display: "flex", flexDirection: "column" }}>
              <CardContent sx={{ flex: "1 0 auto" }}>
                <Typography component="div" variant="h5">
                  Featured Post
                </Typography>
                <Typography
                  variant="subtitle1"
                  color="text.secondary"
                  component="div"
                  mb={2}
                >
                  Nov 12
                </Typography>

                <Typography
                variant="subtitle1"
                color="text.secondary"
                component="div"
                mb={3}
                >
                    This is a wider card with supporting text below as a natural lead-in to additional content.
                </Typography>

                <Link>Continue reading…</Link>
              </CardContent>
            </Box>
            <CardMedia
              component="img"
              sx={{ width: 151 }}
              image={PostImage}
              alt="Post Wallpaper"
            />
          </Card>
        </Grid>
        <Grid item xs={6}>
          <Card sx={{ display: "flex" , justifyContent: "space-between"}}>
            <Box sx={{ display: "flex", flexDirection: "column" }}>
              <CardContent sx={{ flex: "1 0 auto" }}>
                <Typography component="div" variant="h5">
                  Post title
                </Typography>
                <Typography
                  variant="subtitle1"
                  color="text.secondary"
                  component="div"
                  mb={2}
                >
                  Nov 11
                </Typography>
                
                <Typography
                variant="subtitle1"
                color="text.secondary"
                component="div"
                mb={3}
                >
                    This is a wider card with supporting text below as a natural lead-in to additional content.
                </Typography>

                <Link>Continue reading…</Link>
              </CardContent>
            </Box>
            <CardMedia
              component="img"
              sx={{ width: 151 }}
              image={PostImage}
              alt="Post Wallpaper"
            />
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};
