import { Link, Typography } from "@mui/material";
import { Container } from "@mui/system";
import Image from "./../../Assets/images/images.jpg";
import { Info, Wrapper, Background } from "./Hero-style";
import "./Hero-style";

export const Hero = () => {
  return (
    <Container variant="lg">
      <Wrapper>
        <Background src={Image} alt="Image" />
        <Info position="absolute" top={0}>
          <Typography color="white" mb={2} variant="h3">
            Title of a longer featured blog post
          </Typography>

          <Typography color="white" mb={2} variant="h5">
            Multiple lines of text that form the lede, informing new readers
            quickly and efficiently about what's most interesting in this post's
            contents.
          </Typography>

          <Link>Continue reading…</Link>
        </Info>
      </Wrapper>
    </Container>
  );
};
