import { Box, Button, Container, Typography } from '@mui/material'
import SearchIcon from '@mui/icons-material/Search';
import './Header-style'

export const Header = () => {
    return (
        <Container maxWidth="lg">
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom:'2', borderBottom: '1px solid', borderColor: 'rgba(0, 0, 0, 0.12)', marginTop: '10px', paddingBottom: '10px'}}>
            <Button sx={{color: 'blue'}} variant="text">SUBSCRIBE</Button>

            <Typography component="h2"
                variant="h5"
                color="inherit"
                align="center"
                noWrap
                sx={{ flex: 1 }}
            >
                Blog
            </Typography>

            <Box>
                <Button sx={{ borderRadius: '50%', color: 'dimgrey' }} variant="text"><SearchIcon /></Button>

                <Button variant="outlined">sign up</Button>
            </Box>
        </Box>
        </Container>
    )
}